import styled from 'styled-components';

export const WorkSecton = styled.div`
height: auto;
padding: 50px 0;
overflow: hidden;
background: #fff
`


export const WorkTitle=styled.h2`
margin-left: 5%;
font-size: 60px; 
`

export const Span = styled.span`
work .work-title span {
    font-weight: normal
`

export const WorkPart = styled.div`
margin-top: 20px;
    width: 25%;
    float: left;
    height: auto;
    padding: 100px 0;
    border: 1px solid #888;
    box-sizing: border-box;
    text-align: center;
    margin-left: ${props => props.first === 1 ? '5%' : '5%'}

`
export const Icon = styled.i`
color: #888;
margin-bottom: 20px
`




export const PartTitle =  styled.h4`
width: 60%;
margin: auto;
margin-bottom: 20px

`


export const Line =styled.hr`
width: 60%;
margin: auto;
margin-bottom: 20px
`

export const PartDesc=styled.p`
font-size: 14px;
color: #888;
padding: 20px
`

