import styled from 'styled-components';

export const Creative =styled.div`
    height: 500px;
    background: url('https://tse4.mm.bing.net/th?id=OIP.kHpYIA81vtLO5pXjmYzBZgHaB2&pid=Api');
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    margin-top: 0px;

`


export const CreativeInfo = styled.div`
    padding-top: 100px;
    width: 50%;
    float: right;
`

export const InfoTitle = styled.h2`
    font-weight: bold;
    font-size: 30px
`

export const InfotitleSpan= styled.span`
    font-weight: normal
`

export const InfoDir= styled.h4` 
    font-size: 40px;
    color: #eb5424;
    margin-bottom: 20px
`

export const InfoDesc  = styled.p`
    color: #000;
    margin-bottom: 15px;
    line-height: 1.8
`

export const InfoDescA =styled.a`
    text-decoration: none
`